<template>
  <div style="width:98%">
    <h2 style="text-align:center">{{id ? '编辑' : '添加'}}分类</h2>
    <el-form @submit.native.prevent="save" label-width="70px">
      <el-form-item label="所属分类">
        <el-select v-model="model.chrome_category_id" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.chrome_category_id"
            :label="item.chrome_category_title"
            :value="item.chrome_category_id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="软件名称">
        <el-input v-model="model.chrome_name"></el-input>
      </el-form-item>
      <el-form-item label="软件介绍">
        <vue-editor
          useCustomImageHandler
          @image-added="handleImageAdded"
          v-model="model.chrome_content"
        ></vue-editor>
      </el-form-item>
      <el-form-item label="封面图片">
        <el-upload
          class="avatar-uploader"
          :action="$http.defaults.baseURL + '/upload'"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
        >
          <img v-if="model.chrome_img" :src="model.chrome_img" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="发布时间">
        <el-date-picker
          v-model="model.date"
          type="date"
          align="right"
          value-format="yyyy-MM-dd"
          placeholder="选择日期"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="本地文件">
        <el-upload
          class="upload-demo"
          drag
          :action="$http.defaults.baseURL + '/upload'"
          :on-success="chromeFile"
          multiple
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将文件拖到此处，或
            <em>点击上传</em>
          </div>
          <div class="el-upload__tip" slot="tip">{{model.chrome_url}}</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="外部链接">
        <el-input type="text" v-model="model.chrome_url2"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button native-type="submit">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor"; //引入富文本
export default {
  props: {
    id: { type: String }
  },
  data() {
    return {
      model: {},
      options: []
    };
  },
  components: {
    VueEditor
  },
  methods: {
    chromeFile(res) {
     
      this.$set(this.model, "chrome_url", res.url);
    },
    //富文本图片处理
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      var formData = new FormData();
      formData.append("file", file);
      const res = await this.$http.post("upload", formData);
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    //判断编辑还是添加,处理不同的数据
    async save() {
      if (this.id) {
        //有id则发送put
        const res = await this.$http.put(
          "rest/chrome_detail/" + this.id,
          this.model
        );
        if (res.data.code == 200) {
          this.$router.push("/chromeList");
        }
        this.$message({
          type: "success",
          message: res.data.msg
        });
      } else {
        //没有则发送post提交
        const res = await this.$http.post("rest/chrome_detail", this.model);
        if (res.data.code == 200) {
          this.$router.push("/chromeList");
        }
        this.$message({
          type: "success",
          message: res.data.msg
        });
      }

      //
    },
    //进入编辑页面获取数据
    async selectData() {
      const res = await this.$http.get("rest/chrome_detail/" + this.id);
      this.model = res.data[0];
    },
    //获取分类数据数据,渲染select
    async parentCategory() {
      //获取上级分类数据
      const res = await this.$http.get("rest/chrome_category");
      this.options = res.data;
    },

    //封面图片新增model.url
    handleAvatarSuccess(res) {
      this.$set(this.model, "chrome_img", res.url);
    }
  },
  //点击编辑获取原本表单数据,无id不发送
  created() {
    this.parentCategory();
    if (this.id) {
      this.selectData();
    }
  },
  //编辑跳转添加原始数据不消失的处理
  watch: {
    $route() {
      if (!this.id) {
        this.model = {};
      }
    }
  }
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  padding-top: 32px;
  box-sizing: border-box;
  font-size: 28px;
  color: #8c939d;
  width: 200px;
  height: 200px;
  line-height: 200px;
  text-align: center;
}
.avatar {
  width: 200px;
  height: 200px;
  display: block;
}
</style>