import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import chromeCategory from '../components/category/chrome_category.vue'
import chromeAddcategory from '../components/category/chorme_addcategory.vue'

import chromeList from '../components/detail/chromeList.vue'
import addChrome from '../components/detail/addChrome.vue'

import userList from '../components/user/userList.vue'
import addUser from '../components/user/addUser.vue'

import login from '../components/user/login.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path:'',
    redirect:'/login'
  },
  {
    path: '/',
    component: Home,
    children:[

      {path:'/chrome_category',component:chromeCategory},
      {path:'/chrome_addcategory',component:chromeAddcategory},
      {path:'/chrome_addcategory/:id',component:chromeAddcategory,props:true},

      {path:'/chromeList',component:chromeList},
      {path:'/addchrome',component:addChrome},
      {path:'/addchrome/:id',component:addChrome,props:true},

      {path:'/userList',component:userList},
      {path:'/addUser',component:addUser},
      {path:'/addUser/:id',component:addUser,props:true},
     
    ]
  },
  {
    path:'/login',
    component:login,
    meta:{isverify:true}
  }
]

const router = new VueRouter({
  routes,
})

router.beforeEach((to,form,next) => {
 if(!to.meta.isverify && !localStorage.getItem('token')){
  Vue.prototype.$message({
     message:'请先登录'
   })
   router.push('/admin/user/login')
   return
 }
  next()
})

export default router
