<template>
    <el-main>
      <el-table :data="model">
        <el-table-column prop="chrome_category_id" label="ID" width="400">
        </el-table-column>
        <el-table-column prop="chrome_category_title" label="分类名称" width="600">
        </el-table-column>
        <el-table-column label="操作">
            <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="success" size="small">编辑</el-button>
                <el-button @click="deleteClick(scope.row)" type="danger" size="small">删除</el-button>    
            </template>
        </el-table-column>
      </el-table>
     
    </el-main>
</template>

<script>
export default {
    data() {
      return {
        model:[]
      }
    },
    methods:{
     async list() {
        const res = await this.$http.get('rest/chrome_category')
        this.model = res.data
      }, //获取分类列表数据
      handleClick(scope){
          this.$router.push(`/chrome_addcategory/` + scope.chrome_category_id)
      },
      deleteClick(scope){
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
            this.$http.delete('rest/chrome_category/' + scope.chrome_category_id)  //根据id删除
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            this.list()
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            });
      }
      },
    created() {
      this.list()
    }
}
</script>

