<template>
  <el-pagination
    background
    @current-change="currentChange"
    layout="prev, pager, next"
    :total="DetaLen * 10"
  ></el-pagination>
</template>

<script>
export default {
  props: ["pagesize", "chromePage"],
  data() {
    return {
      page: 0,
      len: null
    };
  },
  methods: {
    //用户点击当前页码
    currentChange(userClickpage) {
      this.page = userClickpage;
      this.list();
    },
    //发送当前页码和一页所需数据
    async list() {
      if (this.pagesize) {
        var res = await this.$http.get(
          "/detail/select" + `?page=${this.page}&pagesize=${this.pagesize}`
        );
      }
      if (this.chromePage) {
          var res = await this.$http.get(
            "/chrome/detail/select" +
              `?page=${this.page}&pagesize=${this.chromePage}`
          );
      }
      this.$emit("pageData", res.data.data);
      this.len = res.data.len;
    }
  },
  //总条目数
  computed: {
    DetaLen() {
      return Math.floor(this.len / 50);
    }
  },
  created() {
    this.list();
  }
};
</script>

