<template>
  <el-main>
    <page-nation :chromePage="50"  @pageData="res => model = res" ref="pageNation"></page-nation>
    <el-table :data="model">
      <el-table-column prop="chrome_detail_id" label="ID" width="140"></el-table-column>
      <el-table-column prop="chrome_category_title" label="所属分类"></el-table-column>
      <el-table-column prop="chrome_name" label="软件名称" ></el-table-column>
      <el-table-column prop="chrome_" label="软件名称" ></el-table-column>
      <el-table-column prop="date" label="发布时间"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="success" size="small">编辑</el-button>
          <el-button @click="deleteClick(scope.row)" type="danger" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-main>
</template>

<script>
import pageNation from './componens/pagenation'
export default {
  components:{
      pageNation
  },
  data() {
    return {
      model: []
    };
  },
  methods: {
    handleClick(scope) {
      this.$router.push(`/addchrome/` + scope.chrome_detail_id);
    },
     deleteClick(scope){
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
            this.$http.delete('rest/chrome_detail/' + scope.chrome_detail_id)  //根据id删除
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            this.$refs.pageNation.list()
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            });
      }
  }
};
</script>

